@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {

    body {
        @apply bg-neutral-900
    }

    .sidebar-icon {
        @apply relative flex items-center justify-center w-12 h-12 mx-auto mt-2 mb-2 transition-all duration-300 ease-linear shadow-lg cursor-pointer text-neutral-500 bg-neutral-800 hover:bg-neutral-600 hover:text-white hover:rounded-xl rounded-3xl;
    }

    .sidebar-popup {
        @apply absolute w-auto p-2 m-2 text-xs font-bold text-white transition-all duration-100 origin-left scale-0 rounded-md shadow-md min-w-max left-14 bg-neutral-900;
    }
}

